import React from 'react';
import styled from 'styled-components';
import { HrLine } from '../../components/Common';
import Container from '../../components/Container';
import { Text, Title } from '../../components/Typography';
import { _wl } from '../../utils/useWording';


const DesignSection = ({...props}) => {
  const wording = _wl('homepage.design');

  return (
    <Root {...props}>
      <Wrapper>
        <CloudLeft><img src="/images/mint-cloud-1.png" alt="" /></CloudLeft>
        <Title>{ wording.title }</Title>
        <Text>{ wording.content }</Text>
        <Gallery>
          { wording.gallery.map((item, i) =>
            <li key={i}>
              <img src={item} alt="" />
            </li>
          ) }
        </Gallery>
        <HrLine />
      </Wrapper>
    </Root>
  )
};

const Root = styled.div`
`

const Wrapper = styled(Container)`
  position: relative;
`

const CloudLeft = styled.div`
  position: absolute;
  top: 10%;
  right: -80px;
  width: 240px;
  z-index: 0;
  > img {
    display: block;
    width: 100%;
    height: auto;
  }
`

const Gallery = styled.ul`
  display: flex;
  flex-wrap: wrap;
  margin-top: 36px;
  > li {
    width: 50%;
    padding: 8px;
    box-sizing: border-box;
    > img {
      display: block;
      width: 100%;
      height: auto;
    }
    

  }
`

export default DesignSection;