import React from 'react';
import styled from 'styled-components';
import { CustomOrder, HrLine } from '../../components/Common';
import Container from '../../components/Container';
import { Text, Title } from '../../components/Typography';
import { _wl } from '../../utils/useWording';


const RoadmapSection = ({...props}) => {
  const wording = _wl('homepage.roadmap');

  return (
    <Root {...props}>
      <Wrapper>
        <CloudLeft><img src="/images/roadmap-cloud-1.png" alt="" /></CloudLeft>
        <CloudRight><img src="/images/roadmap-cloud-2.png" alt="" /></CloudRight>
        <Float><img src="/images/roadmap-float.png" alt="" /></Float>
        <Title>{ wording.title }</Title>
        <List>
          { wording.list.map((item, i) =>
            <div key={i} className="item">
              <div className="index">
                <img src={`/images/roadmap-${i+1}.svg`} alt="" />
              </div>
              <CustomOrder className="list">
                { item.map((child, j) =>
                  <li key={j}>
                    <Text className='text'>{ child }</Text>
                  </li>
                ) }
              </CustomOrder>
            </div>
          ) }
        </List>
        <HrLine />
      </Wrapper>
    </Root>
  )
};

const Root = styled.div`
`

const Wrapper = styled(Container)`
  position: relative;
`

const CloudLeft = styled.div`
  position: absolute;
  top: 20px;
  left: -80px;
  width: 180px;
  z-index: 0;
  > img {
    display: block;
    width: 100%;
    height: auto;
  }
`

const CloudRight = styled.div`
  position: absolute;
  bottom: -4%;
  right: -240px;
  width: 600px;
  z-index: 0;
  > img {
    display: block;
    width: 100%;
    height: auto;
  }
`

const Float = styled.div`
  position: absolute;
  bottom: 10px;
  left: -80px;
  width: 120px;
  z-index: 0;
  > img {
    display: block;
    width: 100%;
    height: auto;
  }
`

const List = styled.ul`
  margin-top: 56px;
  .item {
    display: flex;
    margin-bottom: 56px;
    .index {
      width: 120px;
      > img {
        display: block;
        width: 100%;
        height: auto;
      }
    }
    .list {
      width: calc(100% - 120px);
    }
  }
`

export default RoadmapSection;